import React, { Component } from "react";
import Select from 'react-select';
import { Link, withRouter } from 'react-router-dom';
import '../style.css';


const topics = [
  {value: "", label: "All Categories"},
  {value: "Industries", label: "Industries", id: "R79uIjoQaQ9KzvJfyB1H7Ru" },
  {value: "Countries", label: "Countries", id: "R8W91u35GBegWcXXFflYE4" },
  {value: "World Regions", label: "World Regions", id: "R8cndKa2D8NuNg7djwJcXxB" },
  {value: "Trade Regions", label: "Trade Regions", id: "R7ySyiNxcfeZ6bfNjhocNun" },
  {value: "Trade Topics", label: "Trade Topics", id: "RBBed4Voz7iS3nUECA3yzNM" },
]

class SearchBar extends Component {
  constructor() {
    super()
    this.state = {
      queryString: "",
      selectedTopic: {value: "", label: "All Categories"},
    }
    this.handleSelect = this.handleSelect.bind(this)
  };

  handleChange(event) {
    const { name, value } = event.target;
    this.setState({ [name]: value });
  };

  handleSelect(selectedTopic) {
    this.setState({selectedTopic: selectedTopic});
    // console.log(`Topic selected:`, selectedTopic.label);
  } 

  render() {
    return (
      <div>
        <h1>Thesaurus of International Trade Terms</h1>
        <p>The International Trade Administrations (ITA) Thesaurus of International Trade Terms is a controlled and structured list of words and phrases used to tag and index information found on the ITAs websites and databases. The thesaurus covers all subjects related to international trade and foreign investment with particular emphasis on exporting, trade promotion, market access and enforcement and compliance.</p>

        <form onSubmit={(event) => event.preventDefault()} className="center">
          <Select 
            // styles={customStyles}
            options={topics}
            onChange={this.handleSelect} 
            value={this.state.selectedTopic} 
            aria-label="Select a Topic" 
            className="Select"
            classNamePrefix="Select"
          />
          <input 
            type="text" 
            name="queryString" 
            placeholder="Enter search query" 
            aria-label="Enter search query" 
            value={this.state.queryString} 
            onChange={(event) => this.handleChange(event)} 
          />
          <Link to={{pathname: `/search`, search: `&q=${this.state.queryString}&types=${this.state.selectedTopic.value}`}}>
            <button>Search</button>
          </Link>
        </form>
      </div>
    )
  }
}

export default withRouter(SearchBar);

